@tailwind base;
@tailwind components;
@tailwind utilities;

:root{
    --back-color : #5cdb95;
    --back-color-2:#47c982;
    --text-color : #05386b;
}

.parent{
    height: calc(100vh - 100px);
}

.scroll{
    scrollbar-color: gray;
    scrollbar-width: 5px;
}

.scroll::-webkit-scrollbar{
    width: 5px;
}

.scroll::-webkit-scrollbar-thumb{
    background-color: var(--text-color);
    border-radius: 10px;
    height: 5px;
}

@layer components{
    .cardMainPage{
        box-shadow:0px 0px 15px rgb(0,0,0,0.32);
        @apply flex flex-col items-center justify-start text-2xl gap-5 p-2 py-3 rounded-md bg-white;
    }
    .Icons{
        @apply bg-[#aff0cc] p-2 w-fit h-fit rounded-full relative flex items-center text-3xl md:text-2xl cursor-pointer hover:bg-white;
    }
    .HeaderIcons{
        @apply bg-[#aff0cc] p-2 w-fit h-fit text-xl rounded-sm flex items-center hover:bg-white cursor-pointer;
    }
    .Iconstip{
        @apply scale-0 transition-all duration-200 w-fit origin-left bg-black p-2 absolute left-14 text-xs text-white
    }
}

#left-side-menu{
    border-right: 1px solid #379683;
}